import ButtonGroup from "@material-ui/core/ButtonGroup";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import PropTypes from "prop-types";
import YouTubeIcon from "@material-ui/icons/YouTube";
import CodeIcon from "@material-ui/icons/Code";
import React, { Component } from "react";
import "./App.scss";
import ModalVideo from "react-modal-video";
import { Button, Card, withStyles } from "@material-ui/core/";

const data = Object.values(require("./response.json"));
const styles = (theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  courses: {
    display: "flex",
  },
  levels: {
    float: "right",
  },
  iconButtons: {
    width: "50%",
  },
});


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allCourses: data,
      filteredCourses: data,
      selectedCourse: "all",
      selectedLevel: "all",
      isOpen: false,
    };
    this.setCourse = this.setCourse.bind(this);
    this.setLevel = this.setLevel.bind(this);
    this.openModal = this.openModal.bind(this);
  }

  openModal() {
    this.setState({ isOpen: true });
  }

  setCourse(event) {
    const selected = event.currentTarget.value;
    this.setState({ selectedCourse: selected });

    if (selected === "all") {
      if (this.state.selectedLevel === "all") {
        this.setState({ filteredCourses: data });
      } else {
        this.setState({
          filteredCourses: data.filter(
            (course) => course.level === this.state.selectedLevel
          ),
        });
      }
    } else {
      if (this.state.selectedLevel === "all") {
        this.setState({
          filteredCourses: data.filter((course) => course.course === selected),
        });
      } else {
        this.setState({
          filteredCourses: data.filter(
            (course) =>
              course.level === this.state.selectedLevel &&
              course.course === selected
          ),
        });
      }
    }
  }

  setLevel(event) {
    const selected = event.currentTarget.value;
    selected === "all"
      ? this.setState({ selectedLevel: "all" })
      : this.setState({ selectedLevel: Number(event.currentTarget.value) });

    if (this.state.selectedCourse === "all") {
      if (selected === "all") {
        this.setState({ filteredCourses: data });
      } else {
        this.setState({
          filteredCourses: data.filter(
            (course) => course.level === Number(selected)
          ),
        });
      }
    } else {
      if (this.state.selectedLevel === "all") {
        this.setState({
          filteredCourses: data.filter(
            (course) => course.course === this.state.selectedCourse
          ),
        });
      } else {
        this.setState({
          filteredCourses: data.filter(
            (course) =>
              course.level === Number(selected) &&
              course.course === this.state.selectedCourse
          ),
        });
      }
    }
  }

  render() {

    const { classes } = this.props;
    const filteredCourses = this.state.filteredCourses;
    const courses = Array.from(
      new Set(this.state.allCourses.map((project) => project.course))
    );
    const levels = Array.from(
      new Set(this.state.allCourses.map((project) => project.level))
    );

    return (
      <div className="App">
        <ModalVideo
          channel="youtube"
          isOpen={this.state.isOpen}
          videoId="U9t-slLl30E"
          onClose={() => this.setState({ isOpen: false })}
        />
        <ButtonGroup
          variant="contained"
          color="primary"
          aria-label="outlined primary button group"
        >
          <Button onClick={this.setCourse} value="all">
            all
          </Button>
          {courses.map((course, i) => (
            <Button onClick={this.setCourse} value={course} key={i}>
              {course}
            </Button>
          ))}
        </ButtonGroup>
        <ButtonGroup
          variant="contained"
          color="primary"
          aria-label="outlined primary button group"
          className={classes.levels}
        >
          <Button onClick={this.setLevel} value="all">
            all
          </Button>
          {levels.map((level, i) => (
            <Button onClick={this.setLevel} value={level} key={i}>
              level: {level}
            </Button>
          ))}
        </ButtonGroup>

        <Grid container spacing={4}>
          {filteredCourses.map((card, i) => (
            <Grid item key={i} xs={12} sm={6} md={4}>
              <Card className={classes.card}>
                <CardMedia
                  className={classes.cardMedia}
                  image={card.image}
                />
                <CardContent className={classes.cardContent}>
                  <Typography gutterBottom variant="h5" component="h2">
                    {card.name}
                  </Typography>
                  <Typography>
                    {card.description}
                    <br />
                    {card.course} - level:{card.level}
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button
                    size="medium"
                    color="primary"
                    onClick={this.openModal}
                    className={classes.iconButtons}
                  >
                    Preview <YouTubeIcon />
                  </Button>
                  <Button
                    size="medium"
                    color="primary"
                    onClick={this.openModal}
                    className={classes.iconButtons}
                  >
                    Begin Coding
                    <CodeIcon />
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>

        <img
          alt="hw logo"
          src="https://connect-prd-cdn.unity.com/5bb1242c-7440-409a-8ecf-00638c40a97e"
        />
        <h2>Welcome to the Hello World code challenge!</h2>
        <p>
          Here at Hello World, our students learn computer science concepts by
          completing project based lessons. You will build a project display
          page during this code challenge.
        </p>
        <h3>Objective: </h3>
        <ul>
          <li>
            {" "}
            Fork this repo and build a project list page by fulfilling the user
            stories below
          </li>
          <li>
            Your page should show a list of course projects returned from this
            API:{" "}
            <a
              rel="noopener noreferrer"
              target="_blank"
              href="https://hw-challenge-api.firebaseio.com/projects.json"
            >
              https://hw-challenge-api.firebaseio.com/projects.json
            </a>
          </li>
        </ul>
        <h3>Bonus Points: </h3>
        <ul>
          <li>
            Leverage the{" "}
            <a
              href="https://material-ui.com/"
              target="_blank"
              rel="noopener noreferrer"
            >
              Material UI
            </a>{" "}
            component library
          </li>
          <li>
            Test your code using{" "}
            <a
              href="https://testing-library.com/docs/react-testing-library/intro"
              target="_blank"
              rel="noopener noreferrer"
            >
              React Testing Library
            </a>
          </li>
          <li>
            Style your page using{" "}
            <a
              href="https://styled-components.com/"
              target="_blank"
              rel="noopener noreferrer"
            >
              Styled Components
            </a>
          </li>
        </ul>
        <h3>User Stories:</h3>
        <ul>
          <li>As a student, I can see a list of projects</li>
          <li>
            As a student, I can see the title and description of a project
          </li>
          <li>As a student, I can view projects by course</li>
          <li>As a student, I can filter projects by level</li>
          <li>
            As a student, I can click on a project to see an intro video and a
            way to begin the project.
          </li>
        </ul>
        <h3>Rules:</h3>
        <ul>
          <li>Fork this repo and structure your files however you see fit.</li>
          <li>You have 4 hours to complete the challenge.</li>
          <li>When you are ready to submit, open a PR with your changes.</li>
          <li>
            Submit as much as you complete! Just get as much done as you can,
            and also feel free to submit the project early.
          </li>
          <li>
            As long as you write the code yourself, use any resources or
            libraries you need.
          </li>
          <li>
            There are no rules about what the UI should look like, as long as it
            satisfies the user stories above. However, more bonus points are
            awarded for a nice presentation{" "}
            <span aria-label="cool guy emoji" role="img">
              ðŸ˜Ž
            </span>
          </li>
          <li>
            If you have any questions or problems, please reach out to{" "}
            <a href="mailto:ashley@helloworldstudio.org">
              ashley@helloworldstudio.org
            </a>
          </li>
        </ul>

        <h3>Good Luck!</h3>
        <p>- HW Team</p>
      </div>
    );
  }
}
App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);
